import $ from 'jquery';
import ScrollReveal from 'scrollreveal';
import ScrollMagic from 'scrollmagic';
import tilt from "vanilla-tilt";

window.sr = ScrollReveal();

$(document).ready(function() {
    $(".hamburger, .full-overlay").click(function(event) {
        $(".hamburger").toggleClass("h-active");
        $("body").toggleClass("main-nav-active");
    });

    ScrollReveal().reveal('.staggerFadeY', {
        easing: "ease-out",
        opacity: 0,
        distance: '20px',
        duration: 700,
        viewFactor: 0.3,
        delay: 200,
        interval: 80
    });

    ScrollReveal().reveal('.snum', {
        easing: "ease-out",
        opacity: 0,
        distance: '20px',
        duration: 700,
        viewFactor: 0.3,
        delay: 200,
        interval: 80
    });

    ScrollReveal().reveal('.content-section', {
        easing: "ease-out",
        opacity: 0,
        distance: '20px',
        duration: 700,
        viewFactor: 0.1,
        delay: 200,
        interval: 80
    });



    console.log("%c VM Webclusion Pvt Ltd ", "color: white; background: black; padding: 2px 20px; font-size: 13px");
});