import { Component, OnInit, Injectable } from '@angular/core';
import { HttpParams, HttpClient, HttpResponse } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})

@Injectable()
export class HomeComponent implements OnInit {
  totalAngularPackages;
  errorMessage;
  nat: string;
  errors: string;
  isShowIndicator = 1;
  constructor(private http: HttpClient, private route: ActivatedRoute) { }


    ngOnInit() {
      const headers = { 'Authorization': 'Bearer my-token', 'My-Custom-Header': 'foobar' }
      let params = new HttpParams();
       if (this.route.snapshot.params['nat']) {
            /*console.log('id: ', this.route.snapshot.params['nat']); - 1st method*/
            /*2nd method*/
            this.route.params.subscribe(params => {
                if (typeof params['nat'] !== 'undefined') {
                    this.nat = params['nat'];
                }
            }); 
            params = params.append("nat", this.nat);
            params = params.append("results", '50');
       }else{
         params = params.append("gender", "male");
          params = params.append("results", "10");
       }

      this.http.get<any>('https://randomuser.me/api', {headers, params: params}).subscribe(data => {
        this.totalAngularPackages = data.results;
        console.log(this.totalAngularPackages);
      },
      error => {
        this.errors = error;
        console.log(this.errors);
      },
      () => {
        console.log("loaded");
        this.isShowIndicator = 0;
      });


      /*this.http.get('https://randomuser.me/api/').subscribe({
          next: data => {
              this.totalAngularPackages = data.results;
              console.log(this.totalAngularPackages);
          },
          error: error => {
              this.errorMessage = error.message;
              console.error('There was an error!', error);
          }
      })*/
  }

}
